import { Mark, Data } from '@ali/4ever-cangjie';

export interface EmData extends Data {
  value: string;
}

interface Em extends Mark<EmData> {};

const Em = {
  TYPE: 'em',

  create(value: string) {
    return Mark.create<EmData>({
      type: Em.TYPE,
      data: {
        value,
      }
    });
  },

  isEm(maybeEm: any): maybeEm is Em {
    return Mark.isMark(maybeEm) && maybeEm.type === Em.TYPE;
  },
};

export default Em;
